<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Monthly Order (Customer)</span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
      <b-row>
        <b-col style="max-width: max-content !important; min-height:200px !important;">
          <span @click="changeMonthlyOrder('min')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="less-than" size="lg" />
          </span>
        </b-col>
        <b-col style="text-align: center">
          <span>
            <h6 class="isGrey">{{ MonthlyOrder.monthName + ' ' + MonthlyOrder.year }}</h6>
          </span>
        </b-col>
        <b-col style="text-align: right;max-width: max-content !important;">
          <span @click="changeMonthlyOrder('add')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="greater-than" size="lg" />
          </span>
        </b-col>
      </b-row>
      <b-row class="row-dash-new-prospect-top" style="position: absolute; width: 100%; top: 40%;">
        <b-col>
          <div class="buleth__blue" style="cursor: pointer;" @click="showBilledOrder">
            <span>{{ MonthlyOrder.billed_order}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 20px !important;">Billed Order</div>
        </b-col>
        <b-col>
          <div class="buleth__red" style="cursor: pointer;" @click="showUnbilledOrder">
            <span>{{ MonthlyOrder.unbilled_order }}</span>
          </div>
          <div class="buleth-text" style="margin-top: 20px !important;">Unbilled Order</div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      MonthlyOrder: {
        month: "",
        monthName: "",
        year: "",
        billed_order: 0,
        unbilled_order: 0
      }
    };
  },
  methods: {
    showBilledOrder() {
      let param = {
        title: 'Monthly Order Billed (Customer)',
        isBilled: true,
        year: this.MonthlyOrder.year,
        month: this.MonthlyOrder.month,
      };
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "ST_MonthlyOrderCustomer" });
    },
    showUnbilledOrder() {
      let param = {
        title: 'Monthly Order Unbilled (Customer)',
        isBilled: false,
        year: this.MonthlyOrder.year,
        month: this.MonthlyOrder.month,
      };
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "ST_MonthlyOrderCustomer" });
    },
    getMonthlyOrder(date = null) {
      var now = date ? new Date(date) : new Date();
      var month = now.getMonth();
      var year = now.getFullYear();

      this.MonthlyOrder = {
        month: month + 1,
        monthName: this.getMonthName(now),
        year: year,
        billed_order: 0,
        unbilled_order: 0
      };

      var param = {
        option_function_cd: "GetMonthlyOrderCustomer",
        module_cd: "ST",
        ss_portfolio_id: this.getDataUser().portfolio_id,
        user_id: this.getDataUser().user_id,
        years: year,
        months: month + 1
      };
      this.CallFunction(param).then(ress => {
        if (ress == null) return;

        if (ress.Data.length > 0) {
          var data = ress.Data[0];

          this.MonthlyOrder = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            billed_order: data.billed_order,
            unbilled_order: data.unbilled_order
          };
        } else {
          this.MonthlyOrder = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            billed_order: 0,
            unbilled_order: 0
          };
        }
      });
    },
    changeMonthlyOrder(act) {
      var date = new Date();
      if (act === "min") {
        let mindate = this.momentDateFormat(this.dateAdd2("m", date, -12), 'YYYY-MM-01');
        var mth = this.MonthlyOrder.month && this.MonthlyOrder.month !== "" ? parseInt(this.MonthlyOrder.month) : 0;
        var year = this.MonthlyOrder.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = this.dateAdd2("m", frm, -1);
        if (new Date(date) < new Date(mindate)) {
          return
        }
      } else if (act === "add") {
        let maxdate = this.momentDateFormat(this.dateAdd2("m", date, 12), 'YYYY-MM-01');
        var mth = this.MonthlyOrder.month && this.MonthlyOrder.month !== "" ? parseInt(this.MonthlyOrder.month) : 0;
        var year = this.MonthlyOrder.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = this.dateAdd2("m", frm, 1);
        if (new Date(maxdate) < new Date(date)) {
          return
        }
      }

      this.getMonthlyOrder(date);
    }
  },
  mounted() {
    this.getMonthlyOrder();
  }
};
</script>